import React from 'react';
import { motion } from 'framer-motion';
import { useLanguage } from '../contexts/LanguageContext';

const Team = () => {
  const { t } = useLanguage();
  const teamMembers = [
    {
      id: 1,
      name: "Mikail Üstüner",
      role: "Creative Director",
      specialty: "UI/UX Design",
      color: "neon-blue",
      glowColor: "neon-glow-blue",
      shape: "circle"
    },
    {
      id: 2,
      name: "Hamza Kahraman",
      role: "Lead Developer",
      specialty: "Mobile Architecture",
      color: "neon-violet",
      glowColor: "neon-glow-violet",
      shape: "square"
    },
    {
      id: 3,
      name: "Hülya Güneş",
      role: "Game Designer",
      specialty: "Interactive Experiences",
      color: "neon-magenta",
      glowColor: "neon-glow-magenta",
      shape: "triangle"
    },
    {
      id: 4,
      name: "İlayda Bilgili",
      role: "Tech Lead",
      specialty: "Performance Optimization",
      color: "neon-cyan",
      glowColor: "neon-glow-cyan",
      shape: "hexagon"
    }
  ];

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.2,
        delayChildren: 0.3,
      },
    },
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 50, scale: 0.8 },
    visible: {
      opacity: 1,
      y: 0,
      scale: 1,
      transition: {
        duration: 0.6,
        ease: "easeOut",
      },
    },
  };

  const getShapeComponent = (shape, color, glowColor) => {
    const baseClasses = `w-32 h-32 ${glowColor} transition-all duration-300`;
    
    switch (shape) {
      case 'circle':
        return <div className={`${baseClasses} rounded-full bg-gradient-to-br from-${color} to-transparent`} />;
      case 'square':
        return <div className={`${baseClasses} rounded-2xl bg-gradient-to-br from-${color} to-transparent`} />;
      case 'triangle':
        return <div className={`${baseClasses} bg-gradient-to-br from-${color} to-transparent`} style={{ clipPath: 'polygon(50% 0%, 0% 100%, 100% 100%)' }} />;
      case 'hexagon':
        return <div className={`${baseClasses} bg-gradient-to-br from-${color} to-transparent`} style={{ clipPath: 'polygon(30% 0%, 70% 0%, 100% 50%, 70% 100%, 30% 100%, 0% 50%)' }} />;
      default:
        return <div className={`${baseClasses} rounded-full bg-gradient-to-br from-${color} to-transparent`} />;
    }
  };

  return (
    <section className="relative py-20 px-4 overflow-hidden">
      {/* Background */}
      <div className="absolute inset-0 bg-gradient-to-b from-dark-bg to-dark-surface" />
      
      {/* Floating geometric shapes */}
      <div className="absolute inset-0">
        <motion.div
          animate={{ 
            x: [0, 50, 0],
            y: [0, -30, 0],
            rotate: [0, 180, 360]
          }}
          transition={{ duration: 15, repeat: Infinity, ease: "easeInOut" }}
          className="absolute top-20 left-20 w-8 h-8 border-2 border-neon-blue rounded-full opacity-20"
        />
        <motion.div
          animate={{ 
            x: [0, -40, 0],
            y: [0, 40, 0],
            rotate: [360, 180, 0]
          }}
          transition={{ duration: 20, repeat: Infinity, ease: "easeInOut" }}
          className="absolute bottom-32 right-32 w-6 h-6 bg-neon-violet opacity-20"
          style={{ clipPath: 'polygon(50% 0%, 0% 100%, 100% 100%)' }}
        />
        <motion.div
          animate={{ 
            x: [0, 60, 0],
            y: [0, -20, 0],
            rotate: [0, 360, 0]
          }}
          transition={{ duration: 25, repeat: Infinity, ease: "easeInOut" }}
          className="absolute top-1/2 left-10 w-4 h-4 bg-neon-magenta opacity-20"
          style={{ clipPath: 'polygon(30% 0%, 70% 0%, 100% 50%, 70% 100%, 30% 100%, 0% 50%)' }}
        />
      </div>

      <div className="relative z-10 max-w-6xl mx-auto">
        {/* Section header */}
        <motion.div
          initial={{ opacity: 0, y: 50 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true, amount: 0.3 }}
          transition={{ duration: 0.8 }}
          className="text-center mb-16"
        >
          <h2 className="text-4xl md:text-6xl font-bold gradient-text mb-4">
            {t('team.title')}
          </h2>
          <p className="text-xl text-gray-300 max-w-2xl mx-auto">
            {t('team.subtitle')}
          </p>
          <div className="w-24 h-1 bg-gradient-to-r from-neon-blue to-neon-violet mx-auto rounded-full mt-6" />
        </motion.div>

        {/* Team grid */}
        <motion.div
          variants={containerVariants}
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true, amount: 0.2 }}
          className="grid md:grid-cols-2 lg:grid-cols-4 gap-8"
        >
          {teamMembers.map((member) => (
            <motion.div
              key={member.id}
              variants={itemVariants}
              whileHover={{ 
                scale: 1.05,
                y: -10,
                transition: { duration: 0.3 }
              }}
              className="group text-center"
            >
              {/* Abstract silhouette */}
              <motion.div
                whileHover={{ 
                  scale: 1.1,
                  rotate: 5,
                  boxShadow: `0 0 30px var(--tw-color-${member.color})`
                }}
                className="flex justify-center mb-6"
              >
                {getShapeComponent(member.shape, member.color, member.glowColor)}
              </motion.div>

              {/* Member info */}
              <div className="glass rounded-2xl p-6 hover-lift">
                <h3 className={`text-xl font-bold text-${member.color} mb-2`}>
                  {member.name}
                </h3>
                <p className="text-neon-cyan font-semibold mb-2">
                  {member.role}
                </p>
                <p className="text-gray-400 text-sm">
                  {member.specialty}
                </p>

                {/* Hover effect overlay */}
                <motion.div
                  initial={{ opacity: 0 }}
                  whileHover={{ opacity: 1 }}
                  className={`absolute inset-0 bg-gradient-to-br from-${member.color}/10 to-transparent rounded-2xl`}
                />
              </div>
            </motion.div>
          ))}
        </motion.div>

        {/* Team culture section */}
        <motion.div
          initial={{ opacity: 0, y: 50 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true, amount: 0.3 }}
          transition={{ duration: 0.8, delay: 0.5 }}
          className="mt-20 text-center"
        >
          <div className="glass rounded-2xl p-12 max-w-4xl mx-auto">
            <h3 className="text-3xl font-bold gradient-text mb-6">
              {t('team.culture.title')}
            </h3>
            <p className="text-gray-300 text-lg leading-relaxed mb-8">
              {t('team.culture.description')}
            </p>
            
            {/* Culture values */}
            <div className="grid md:grid-cols-3 gap-6 mt-8">
              {[
                { value: t('team.culture.values.innovation'), icon: "💡" },
                { value: t('team.culture.values.collaboration'), icon: "🤝" },
                { value: t('team.culture.values.excellence'), icon: "⭐" }
              ].map((item, index) => (
                <motion.div
                  key={index}
                  whileHover={{ scale: 1.05 }}
                  className="text-center"
                >
                  <div className="text-3xl mb-2">{item.icon}</div>
                  <div className="text-neon-cyan font-semibold">{item.value}</div>
                </motion.div>
              ))}
            </div>
          </div>
        </motion.div>
      </div>
    </section>
  );
};

export default Team;
